<template>
  <div class="row">
    <div class="col-md-12 card p-0">
      <div class="card-body p-0">
        <div class="row">
          <div class="col-lg-9 col-md-6 col-sm-6 col-6">
            <my-filter :rules="rules" v-on:filter-change="updateFilter"></my-filter>
          </div>
          <div class="col-lg-3 col-md-6 col-sm-6 col-6">
            <column-toggle
              v-model="columnDefs"
              @input="cols => this.$refs['table'].updateColumns(cols)"
            ></column-toggle>
          </div>
        </div>
        <div class="col-sm-12 p-0">
          <my-table
            ref="table"
            :columnDefs="columnDefs"
            v-bind:data-rows="clients"
            :actions="actions"
            :actionsTable="actionsTable"
            @sortChange="sortChange"
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import { MessageBox, Message } from 'element-ui'
  import MyTable from 'src/components/UIComponents/Table.vue'
  import MyFilter from 'src/components/UIComponents/Filter.vue'
  import clientSchemas from './client-schemas'
  import dtHelper from 'src/helpers/datatable'
  import ColumnToggle from 'src/components/UIComponents/ColumnToggle'

  export default{
    components: {
      MyTable,
      MyFilter,
      ColumnToggle
    },
    computed: {
      clients() {
        let rows = this.$store.state.clients;
        return dtHelper.filterByRules(rows, this.filterOutput);
      }
    },
    data () {
      let initFiledArrays = ['id', 'logo', 'name', 'updated_at', 'status'];
      let columnDefs = dtHelper.buildInitFields(clientSchemas, initFiledArrays);

      return {
        filterOutput: [],
        columnDefs: columnDefs,
        actions: [
          {
            type: 'primary',
            icon: 'nc-icon nc-ruler-pencil',
            callback: this.edit
          },
          {
            type: 'danger',
            icon: 'nc-icon nc-simple-remove',
            callback: this.remove
          },
        ],
        actionsTable: [
          {
            title: 'Ẩn',
            callback: this.inactiveAll
          },
          {
            title: 'Hiện',
            callback: this.activeAll
          },
          {
            title: 'Xóa',
            color: 'text-danger',
            callback: this.removeAll
          }
        ],
        filter: {},
        rules: dtHelper.buildRules(clientSchemas)
      }
    },
    mounted() {
      window.AP = this;
      this.$store.dispatch('fetchClient');

      this.$store.dispatch('setPageTitle', 'khách hàng tiêu biểu');
      this.$store.dispatch('setCurrentActions', [{
        label: 'Tạo mới',
        type: 'primary',
        icon: '',
        callback: this.create
      }])
    },
    methods: {
      create(){
        this.$router.push('/client/create');
      },
      edit(index, row){
        this.$router.push('/client/' + row.id);
      },
      inactiveAll(rows){
        this.updateStatus(rows, 'inactive');
      },
      activeAll(rows){
        this.updateStatus(rows, 'active');
      },
      remove(index, row){
        MessageBox.confirm('Bạn có chắc chắn xóa không?', 'Warning', {
          confirmButtonText: 'Đồng ý',
          cancelButtonText: 'Hủy bỏ',
          type: 'warning',
          center: true
        }).then(() => {
          this.updateStatus([row], 'delete');
        })
      },
      removeAll(rows){
        MessageBox.confirm('Bạn có chắc chắn xóa không?', 'Warning', {
          confirmButtonText: 'Đồng ý',
          cancelButtonText: 'Hủy bỏ',
          type: 'warning',
          center: true
        }).then(() => {
          this.updateStatus(rows, 'delete');
        })
      },
      updateStatus(rows, status){
        let self = this;
        this.$util.updateStatusAll('client', rows, status).then(function (result) {
          self.$store.dispatch('fetchClient');
          Message({
            type: 'success',
            message: 'Cập nhật thành công'
          });
        }).catch(function (error) {
          Message({
            type: 'error',
            message: error.message
          });
        });
      },
      updateFilter(filterOutput) {
        this.filterOutput = filterOutput;
      },
      sortChange(data){
        let prop = data.prop;
        let order = data.order == 'ascending' ? 'asc' : 'desc';
        this.$store.dispatch('fetchClient', {
          order: prop + '=' + order
        });
      },
    }
  }
</script>
<style lang="scss">
  .el-table .td-actions{
    button.btn {
      margin-right: 5px;
    }
  }
</style>
